.background {
    width: 100%; 
    height: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    overflow: scroll;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: url(./images/cursor.png), auto;
    transition: background-color 0.09s ease-in;
    -webkit-font-smoothing: antialiased;}

.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 90vh;
    color: white;
    font-family: sofia-pro-soft, sans-serif;
    font-weight: 600;
    font-style: normal;
    text-decoration: none;}

.nav {
    display: flex;
    justify-content: space-between;}

a {
    color: inherit;
    text-decoration: inherit;
    cursor: inherit;} 

.projects {
    display: flex;
    justify-content: space-between;
    align-items: start;
}

.project {
    font-size: 67%;
}

.octocat {
    height: 9vh;
    vertical-align: sub;
}

.techs {
    display: flex;
    justify-content: space-between;
}

.tech {
    height: 9vh;
    vertical-align: sub;
}

/* .quote {
    font-family: felt-tip-roman, serif;
    font-weight: 800;
} */

/* Most Phones || 0px (0em) > 768px (48em) */
@media (max-width: 767px) and (orientation: landscape){
    .background {
        width: 100vw;

      }
      .container {
        margin-left: 2em;
        margin-right: 2em;
        font-size: 3vw;
    }

    .heading {
        font-size: 6vw;
        margin-top: .15em;
        margin-bottom: .15em;
    }

    .text {
        margin-top: .15em;
        margin-bottom: .15em;
    }}

/* Most Phones || 0px (0em) > 768px (48em) */
@media (max-width: 767px) and (orientation: portrait) {
    .container {
        margin-left: 1em;
        margin-right: 1em;
        font-size: 7.5vw;
    }

    .heading {
        font-size: 17vw;
        margin-top: .15em;
        margin-bottom: .33em;
    }

    .nav {
        margin-top: .55em;
        margin-bottom: .5em;
        font-size: 17vw;
        flex-direction: column;
        align-items: center;
    }}

/* Most Tablets || 768px (48em) > 1376px (86em) */
@media (min-width: 48em) and (max-width: 86em) and (orientation: landscape){
    .container {
        height: 90vh;
        width: 90vw;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 1em;
        margin-right: 1em;
        font-size: 4vw;
    }

    .heading {
        font-size: 8vw;
        margin-top: .15em;
        margin-bottom: .15em;
    }

    .text {
        margin-top: 0em;
        margin-bottom: 0em;
    }
    
    .nav {
        justify-content: space-between;
        margin-bottom: .15em;
    }}

/* CSS to "Force" Landscape Orientation */
@media (min-width: 48em) and (max-width: 86em) and (orientation: portrait) {
    
    .container {
        width: 90vh;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 1em;
        margin-right: 1em;
        font-size: 4vh;
    }

    .heading {
        font-size: 10vh;
        margin-top: .15em;
        margin-bottom: .15em;
    }

    .text {
        margin-top: 0em;
        margin-bottom: 0em;
    }

    .nav {
        margin-top: .55em;
        margin-bottom: .5em;
        font-size: 10vw;
        flex-direction: column;
        /* align-items: center; */
    }}

/* laptops || 1376px (86em) > 1920px (120em) > 2240px (140em) */
@media (min-width: 86em) {
    .container {
        max-width: 1140px;
        height: 90vh;
        width: 80vw;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 1em;
        margin-right: 1em;
        font-size: 5vh;
    }

    .heading {
        font-size: 9vh;
        margin-top: .2em;
        margin-bottom: .2em;
    }

    .text {
        margin-top: .1em;
        margin-bottom: .1em;
    }

    .nobreak {
        white-space: nowrap;
    }}

@media (min-width: 120em) {
    .container { max-width: 1220px; }}

@media (min-width: 140em) {
    .container { max-width: 1600px; }}

/* 4k displays || 2880px */
@media (min-width: 180em) {
    .container { max-width: 2400px; }}